import React, {useEffect} from 'react'
import "./Experience.css"
import Aos from "aos"
import "aos/dist/aos.css"

export const Experience = () => {

    useEffect(() => {
        Aos.init({duration: 1000, once: false})
    }, [])

    return (
        <div className="experience-card">
            <div data-aos="fade-right" data-aos-delay="300" className="exp-col">
                <div className="exp-mini-card">
                    <p className="exp-number-big">35+</p>
                    <p className="exp-text">Años de Experiencia</p>
                </div>
            </div>
            <div data-aos="fade-right" data-aos-delay="600" className="exp-col">
                <div className="exp-mini-card">
                    <p className="exp-number-big">450+</p>
                    <p className="exp-text">Proyectos Completados</p>
                </div>
            </div>
            <div data-aos="fade-right" data-aos-delay="900" className="exp-col">
                <div className="exp-mini-card">
                    <p className="exp-number-big">350+</p>
                    <p className="exp-text">Clientes Satisfechos</p>
                </div>

            </div>
            
        </div>
    )
}
